// Define Custom Elements
// -----------------------------------------------------------
class wrapper extends HTMLElement { constructor() { super(); } }
customElements.define("penta-yazilim", wrapper);

// GSAP SCRIPTS
// -----------------------------------------------------------

// SCROLLABLE SELECTOR CLICK (TIKLAYINCA data-scrollable-section ALANINA SCROLL EDER)
// -----------------------------------------------------------
if (document.querySelector('.scrollable-selector')) {
    document.querySelectorAll(".scrollable-selector").forEach((element, index) => {
        element.addEventListener("click", () => {
            let scrollableSection = element.getAttribute('data-scrollable-section');
            gsap.to(window, { duration: .65, scrollTo: { y: scrollableSection, offsetY: 10 } });
        });
    });
}

// Number Counter Effect
// -----------------------------------------------------------
document.fonts.ready.then(() => {
    if (window.innerWidth > mobileLimit) {
        gsap.utils.toArray(".fx-text-counter").forEach(element => {
            let value = element.querySelector('.fx-child');
            let valueText = value.textContent;
            let numbers = valueText.split('');
            let valueHeight = value.offsetHeight;

            let numberElement = '<div class="digit" style="height: ' + valueHeight + 'px;"></div>';
            for (let i = 0; i <= 9; i++) { numberElement += '<div class="digit w-fit leading-none" style="height: ' + valueHeight + 'px;">' + i + '</div>'; }

            value.innerHTML = '';
            value.style.height = valueHeight + 'px';
            numbers.forEach(number => {
                if (number == '.' || number == ',' || number == 'm' || number == 'k') value.innerHTML += '<div class="point inline-flex flex-col leading-none [will-change:transform,width] backface-hidden translate-y-[-15%]">' + number + '</div>';
                else value.innerHTML += '<div data-number="' + (parseInt(number) + 1) + '" class="number inline-flex flex-col [will-change:transform,width] backface-hidden">' + numberElement + '</div>';
            });

            value.querySelectorAll('.number').forEach((number, index) => {
                number.querySelectorAll('.digit').forEach(digit => { if (digit.textContent == number.dataset.number - 1) { digit.classList.add('active'); } });

                gsap.to(number, {
                    scrollTrigger: {
                        trigger: element,
                        start: 'top-=' + (window.innerHeight / 3) + ' center',
                    },
                    duration: 2,
                    y: -number.dataset.number * valueHeight,
                    width: number.querySelector('.digit.active').offsetWidth,
                    ease: 'expo.out',
                    delay: index * 0.1
                })
            });
        });
    }
});

// Smart Menu
// -----------------------------------------------------------
let headerField = document.querySelector('.header-field');
let headerHeight = headerField.offsetHeight;
let currentScrollTop = window.pageYOffset || document.documentElement.scrollTop;
if (currentScrollTop >= (headerHeight / 5)) { headerField.classList.add('is-hidden'); headerField.classList.add('is-fixed'); }
else { headerField.classList.remove('is-hidden'); headerField.classList.remove('is-fixed'); }

let lastScrollTop = 0;
let stickyObjects = document.querySelectorAll('.sticky-el');
window.addEventListener('scroll', function () {
    let scrollTop = window.pageYOffset || document.documentElement.scrollTop;
    if (scrollTop > lastScrollTop && scrollTop > (headerHeight / 5)) {
        stickyObjects.forEach(el => { el.style.top = `45px`; });
        headerField.classList.add('is-hidden'); headerField.classList.add('is-fixed');
    }
    else {
        stickyObjects.forEach(el => { el.style.top = `${headerHeight + 45}px`; });
        if (scrollTop > (headerHeight / 5)) { headerField.classList.remove('is-hidden'); headerField.classList.add('is-fixed'); }
        else { headerField.classList.remove('is-hidden'); headerField.classList.remove('is-fixed'); }
    }
    lastScrollTop = scrollTop;
}, false);

// DYNAMIC STICKY
// -----------------------------------------------------------
let dynamicSticky = document.querySelectorAll('.dynamic-sticky');
if (dynamicSticky) {
    dynamicSticky.forEach(element => {
        let headerHeight = document.querySelector('.header-field').offsetHeight; // HEADER HEIGHT
        // MOUSE YUKARI SCROLL
        let lastScrollTop = 0;
        window.addEventListener('scroll', function () {
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop;
            if (scrollTop > lastScrollTop) {
                element.classList.add('is-sticky', 'top-60');
            }
            else {
                element.classList.remove('is-sticky', 'top-60');
            }
            lastScrollTop = scrollTop;
        }, false);

        // MOUSE AAI SCROLL
        let lastScrollBottom = 0;
        window.addEventListener('scroll', function () {
            let scrollBottom = window.pageYOffset || document.documentElement.scrollTop;
            if (scrollBottom > lastScrollBottom) {
                element.classList.remove('is-sticky-bottom');
                element.style.top = '60px'
            }
            else {
                element.classList.add('is-sticky-bottom');
                element.style.top = `${headerHeight + 30}px`;
            }
            lastScrollBottom = scrollBottom;
        }, false);
    });
}

// HEADER SPACE FIX
// -----------------------------------------------------------
let headerSpacePadding = document.querySelectorAll('.hs-padding');
if (headerSpacePadding.length) {
    headerSpacePadding.forEach(element => {
        element.style.paddingTop = `${document.querySelector('header').offsetHeight}px`;
    });
}
let headerSpaceMargin = document.querySelectorAll('.hs-margin');
if (headerSpaceMargin.length) {
    headerSpaceMargin.forEach(element => {
        element.style.marginTop = `${document.querySelector('header').offsetHeight}px`;
    });
}

// COOKIE
// -----------------------------------------------------------
let cookie = document.querySelector('.cookie-box');
let cookieClose = document.querySelectorAll('.close-cookie');
let cookieAccept = document.querySelectorAll('.accept-cookie');

cookieClose.forEach(close => {
    close.addEventListener('click', () => {
        cookie.classList.add('!translate-y-[125%]');
    });
});

(Cookies.get('cookie') && Cookies.get('cookie') == 'accepted') ? '' : cookie.classList.remove('translate-y-[125%]');

cookieAccept.forEach(close => {
    close.addEventListener('click', () => {
        cookie.classList.add('!translate-y-[125%]');
        Cookies.set('cookie', 'accepted', { expires: 7 });
    });
});

