// MENU
// ------------------------------------------------------------------
let headerField = document.querySelector('.header-field');
let body = document.querySelector('body');
let mobileMenuSelector = document.querySelector('.mobile-menu-selector');
let menuField = document.querySelector('.menu-ul');
let bgOverlayGeneral = document.querySelector('.bg-overlay-general');

mobileMenuSelector.addEventListener('click', function () {
    mobileMenuSelector.classList.toggle('is-active');
    menuField.classList.toggle('is-active');
    bgOverlayGeneral.classList.toggle('is-active');
    headerField.classList.toggle('is-active');
    body.classList.toggle('overflow-hidden');
    if (window.innerWidth > 1024) {
        if (mobileMenuSelector.classList.contains('is-active')) {
            // smoother.paused(true);
        } else {
            // smoother.paused(false);
        }
    }
});

bgOverlayGeneral.addEventListener('click', function () {
    mobileMenuSelector.classList.remove('is-active');
    menuField.classList.remove('is-active');
    bgOverlayGeneral.classList.remove('is-active');
    headerField.classList.remove('is-active');
    body.classList.remove('overflow-hidden');
    if (window.innerWidth > 1024) {
        if (mobileMenuSelector.classList.contains('is-active')) {
            // smoother.paused(true);
        } else {
            // smoother.paused(false);
        }
    }
});

let hasSubMenu = document.querySelectorAll('.has-sub-menu');

if (window.innerWidth > 1280) {
    let isTransparentCheck = false;
    let isBGWhiteCheck = false;
    hasSubMenu.forEach(function (item, index) {
        item.addEventListener('mouseenter', function () {
            if (headerField.classList.contains('is-transparent')) {
                isTransparentCheck = true;
                headerField.classList.remove('is-transparent');
                headerField.classList.add('bg-white');
            } else if (headerField.classList.contains('bg-white')) {
                isBGWhiteCheck = true;
            } else {
                isTransparentCheck = false;
                headerField.classList.add('bg-white');
            }
            bgOverlayGeneral.classList.add('is-active');
        });
        item.addEventListener('mouseleave', function () {
            if (isTransparentCheck == true) {
                headerField.classList.add('is-transparent');
                headerField.classList.remove('bg-white');
                isTransparentCheck = true;
            }
            if (isBGWhiteCheck == true) {
                headerField.classList.add('bg-white');
            }
            bgOverlayGeneral.classList.remove('is-active');
        });
    });
}

let subTrigger = document.querySelectorAll('.sub-trigger');
subTrigger.forEach(function (item, index) {
    item.addEventListener('click', function () {
        item.classList.toggle('is-active');
        item.nextElementSibling.classList.toggle('is-active');
    });
});

let menuItem = document.querySelectorAll('.menu-item');
menuItem.forEach(function (item, index) {
    if (window.innerWidth > 1024) {
        item.addEventListener('mouseenter', function () {
            // smoother.paused(true);
        });
        item.addEventListener('mouseleave', function () {
            // smoother.paused(false);
        });
    }
});

// BACKGROUND SVG | ANIMATION
// ------------------------------------------------------------------
if (document.querySelector('.background-svg')) {
    setTimeout(() => {
        document.querySelector('.background-svg').classList.add('is-active');
    }, 1000);
}

// FORM ITEM
// ------------------------------------------------------------------
if (document.querySelector('.form-item')) {
    let formItem = document.querySelectorAll('.form-item');

    formItem.forEach(item => {
        if (item.querySelector('.form-el')) {
            let input = item.querySelector('.form-el');
            input.addEventListener('focus', function () {
                item.classList.add('is-focus');
                if (input.value != '') {
                    item.classList.add('is-focus');
                } else {
                    item.classList.remove('is-focus');
                }
            });

            input.addEventListener('blur', function () {
                item.classList.remove('is-focus');
                if (input.value != '') {
                    item.classList.add('is-focus');
                } else {
                    item.classList.remove('is-focus');
                }
            });
        }
    });
}



