if (window.innerWidth > 1024) {
    // SUB MENU | TYPE 1
    // ------------------------------------------------------------------
    if (window.innerWidth > 1280) {
        // SUB MENU | TYPE 1
        let subMenuOne = document.querySelectorAll('.sub-menu.type-1');
        subMenuOne.forEach(function (item) {
            // VARIABLES
            let subMenuUL = item.querySelector('.sub-menu-ul');
            let container = item.querySelector('.container');
            let wrapper = item.querySelector('.wrapper');
            let contentBackground = item.querySelector('.content-background');
            let subMenuLink = item.querySelectorAll('.sub-menu-link');
            let remainWidth = (window.innerWidth - wrapper.offsetWidth) / 2;

            // BACKGROUND
            contentBackground.style.width = "calc(100% + " + remainWidth + "px)";
            if (document.querySelector('html').getAttribute('dir') == 'ltr') {
                contentBackground.style.left = -remainWidth + 'px';
            }

            // CONTENT SPLIT
            let contentSplitWrapper = item.querySelector('.content-split-wrapper');
            let contentSplitField = item.querySelector('.content-split-field');
            let contentSplit = item.querySelectorAll('.content-split');
            contentSplitWrapper.style.maxWidth = remainWidth - 30 + 'px';
            if (document.querySelector('html').getAttribute('dir') == 'ltr') {
                contentSplitWrapper.style.left = -remainWidth + 'px';
            } else {
                contentSplitWrapper.style.right = -remainWidth + 'px';
            }

            // SUB MENU CONTENT
            let subMenuContent = item.querySelectorAll('.sub-menu-content');
            subMenuContent.forEach(function (content, index) {
                content.classList.remove('is-active');
            });

            // MOUSE LEAVE | ALL ITEM REMOVE, FIRST ITEM ACTIVE
            // item.addEventListener('mouseleave', function() {
            //     subMenuLink.forEach(function(link) {
            //         link.classList.remove('is-active');
            //     });
            //     subMenuIMG.forEach(function(img) {
            //         img.classList.remove('is-active');
            //     });
            //     subMenuLink[0].classList.add('is-active');
            //     subMenuIMG[0].classList.add('is-active');
            // });

            // FIRST ITEM ACTIVE
            subMenuLink[0].classList.add('is-active');
            contentSplit[0].querySelector('.split').style.width = "100%";
            subMenuContent[0].classList.add('is-active');
            subMenuContent[0].querySelectorAll('.sub-menu-content-link')[0].classList.add('is-active');
            subMenuContent[0].querySelectorAll('.sub-menu-main-field')[0].classList.add('is-active');
            // subMenuContent[0].querySelectorAll('.sub-menu-image')[0].style.pointerEvents = "auto";

            // MOUSE ENTER | ITEM ACTIVE
            subMenuLink.forEach(function (element, index) {
                element.addEventListener('mouseenter', function () {
                    subMenuLink.forEach(function (link) {
                        link.classList.remove('is-active');
                    });
                    element.classList.add('is-active');

                    element.closest('.sub-menu').querySelectorAll('.sub-menu-content').forEach(function (content, indexey) {
                        content.classList.remove('is-active');
                        content.querySelectorAll('.sub-menu-content-link').forEach(function (link) {
                            link.classList.remove('is-active');
                        });
                        content.querySelectorAll('.sub-menu-main-field').forEach(function (field) {
                            field.classList.remove('is-active');
                        });
                    });
                    element.closest('.sub-menu').querySelectorAll('.sub-menu-content')[index].classList.add('is-active');
                    element.closest('.sub-menu').querySelectorAll('.sub-menu-content')[index].querySelectorAll('.sub-menu-content-link')[0].classList.add('is-active');
                    element.closest('.sub-menu').querySelectorAll('.sub-menu-content')[index].querySelectorAll('.sub-menu-main-field')[0].classList.add('is-active');

                    contentSplit.forEach(function (split) {
                        split.querySelector('.split').style.width = 0;
                    });
                    contentSplit[index].querySelector('.split').style.width = "100%";
                });
            });

            // SUB MENU CONTENT TREE | STEP 1
            subMenuContent.forEach(function (content) {
                let subMenuContentLink = content.querySelectorAll('.sub-menu-content-link');
                subMenuContentLink.forEach(function (link, index) {
                    // EER TIKLAMA LE DEECEKSE
                    if (link.classList.contains('is-clickable')) {
                        link.addEventListener('click', function () {
                            subMenuContentLink.forEach(function (link) {
                                link.classList.remove('is-active');
                            });
                            link.classList.add('is-active');

                            let subMenuMainField = link.closest('.sub-menu-content').querySelectorAll('.sub-menu-main-content .sub-menu-main-field');
                            subMenuMainField.forEach(function (field) {
                                field.classList.remove('is-active');
                            });
                            subMenuMainField[index].classList.add('is-active');
                        });
                    }
                    // EER HOVER LE DEECEKSE
                    else {
                        link.addEventListener('mouseenter', function () {
                            subMenuContentLink.forEach(function (link) {
                                link.classList.remove('is-active');
                            });
                            link.classList.add('is-active');

                            let subMenuMainField = link.closest('.sub-menu-content').querySelectorAll('.sub-menu-main-content .sub-menu-main-field');
                            subMenuMainField.forEach(function (field) {
                                field.classList.remove('is-active');
                            });
                            subMenuMainField[index].classList.add('is-active');
                        });
                    }
                });
            });

            let subMenuContentLink = document.querySelectorAll('.sub-menu-content-link');
            let subMenuMainLink = document.querySelectorAll('.sub-menu-main-link');
            let subMenuImageLeft = document.querySelectorAll('.sub-menu-image-left');

            subMenuMainLink.forEach(function (link) {
                link.addEventListener('mouseenter', function () {
                    let index = [...link.closest('.sub-menu-main-field').querySelectorAll('.sub-menu-main-link')].indexOf(link);
                    link.closest('.sub-menu-main-field').querySelectorAll('.sub-menu-image').forEach(function (img) {
                        img.classList.remove('is-active');
                    });
                    link.closest('.sub-menu-main-field').querySelectorAll('.sub-menu-image')[index].classList.add('is-active');
                })
            });

            subMenuUL.addEventListener('scroll', function () {
                let scrollPosition = subMenuUL.scrollTop;
                contentSplitField.scrollTop = scrollPosition;
            });
        });
    }

    // SUB MENU | TYPE 2
    // ------------------------------------------------------------------
    let subMenuTwo = document.querySelectorAll('.sub-menu.type-2');

    subMenuTwo.forEach(function (item) {
        // VARIABLE
        let subMenuUL = item.querySelector('.sub-menu-ul');
        let container = item.querySelector('.container');
        let wrapper = item.querySelector('.wrapper');
        let contentBackground = item.querySelector('.content-background');
        let subMenuLink = item.querySelectorAll('.sub-menu-link');
        let subMenuIMG = item.querySelectorAll('.sub-menu-image');
        let remainWidth = (window.innerWidth - wrapper.offsetWidth) / 2;

        // BACKGROUND
        contentBackground.style.width = "calc(100% + " + remainWidth + "px)";
        if (document.querySelector('html').getAttribute('dir') == 'ltr') {
            contentBackground.style.left = -remainWidth + 'px';
        }

        // CONTENT SPLIT
        let contentSplitWrapper = item.querySelector('.content-split-wrapper');
        let contentSplitField = item.querySelector('.content-split-field');
        let contentSplit = item.querySelectorAll('.content-split');
        contentSplitWrapper.style.maxWidth = remainWidth - 30 + 'px';
        if (document.querySelector('html').getAttribute('dir') == 'ltr') {
            contentSplitWrapper.style.left = -remainWidth + 'px';
        } else {
            contentSplitWrapper.style.right = -remainWidth + 'px';
        }

        // MOUSE LEAVE | ALL ITEM REMOVE, FIRST ITEM ACTIVE
        item.addEventListener('mouseleave', function () {
            subMenuLink.forEach(function (link) {
                link.classList.remove('is-active');
            });
            subMenuIMG.forEach(function (img) {
                img.classList.remove('is-active');
            });
            subMenuLink[0].classList.add('is-active');
            subMenuIMG[0].classList.add('is-active');
        });

        // FIRST ITEM ACTIVE
        subMenuLink[0].classList.add('is-active');
        subMenuIMG[0].classList.add('is-active');
        contentSplit[0].querySelector('.split').style.width = "100%";

        // MOUSE ENTER | ITEM ACTIVE
        subMenuLink.forEach(function (element, index) {
            element.addEventListener('mouseenter', function () {
                subMenuLink.forEach(function (link) {
                    link.classList.remove('is-active');
                });
                element.classList.add('is-active');

                subMenuIMG.forEach(function (img) {
                    img.classList.remove('is-active');
                });
                subMenuIMG[index].classList.add('is-active');

                contentSplit.forEach(function (split) {
                    split.querySelector('.split').style.width = 0;
                });
                contentSplit[index].querySelector('.split').style.width = "100%";
            });
        });

        subMenuUL.addEventListener('scroll', function () {
            let scrollPosition = subMenuUL.scrollTop;
            contentSplitField.scrollTop = scrollPosition;
        });
    });
}