// Swiper -----------------------------------------------------------
import Swiper from 'swiper';
import { Navigation, Pagination, EffectCreative, EffectCoverflow, EffectFade, EffectCards, Parallax, Autoplay, Grid, Thumbs } from 'swiper/modules';
window.Swiper = Swiper;
window.N = Navigation;
window.P = Pagination;
window.EC = EffectCreative;
window.EF = EffectFade;
window.ECo = EffectCoverflow;
window.ECa = EffectCards;
window.Px = Parallax;
window.A = Autoplay;
window.G = Grid;
window.T = Thumbs;


// OTHER PRODUCT CAROUSEL | 
// ------------------------------------------------------------------
if (document.querySelector('.other-product-carousel')) {
    let otherProductCarousel = new Swiper(".other-product-carousel", {
        modules: [A, N],
        slidesPerView: 1,
        speed: 1000,
        spaceBetween: 30,
        loop: true,
        resistance: true,
        resistanceRatio: 0,
        watchSlidesProgress: true,
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
            pauseOnMouseEnter: true,
        },
        navigation: {
            nextEl: ".other-product-next",
            prevEl: ".other-product-prev"
        },
        breakpoints: {
            0: {
                slidesPerView: 1,
            },
            769: {
                slidesPerView: 1.485,
            },
        },
    });
    
    // PAUSE ON MOUSE ENTER
    otherProductCarousel.autoplay.stop();
    let otherSection = document.querySelector('.other-product-section');
    let otherSlide = document.querySelectorAll('.other-product-carousel .swiper-slide');
    if (window.innerWidth > 1024) {
        otherSection.addEventListener('mouseenter', function () {
            otherProductCarousel.autoplay.stop();
        });
        otherSection.addEventListener('mouseleave', function () {
            otherProductCarousel.autoplay.start();
        });

        otherSlide.forEach(function (item, index) {
            gsap.to(item, {
                scrollTrigger: {
                    trigger: item,
                    start: "top-=300px center",
                    end: "bottom+=300px center",
                    scrub: true,
                    onEnter: function () {
                        otherProductCarousel.autoplay.start();
                    },
                    onLeave: function () {
                        otherProductCarousel.autoplay.stop();
                    },
                    onEnterBack: function () {
                        otherProductCarousel.autoplay.start();
                    },
                    onLeaveBack: function () {
                        otherProductCarousel.autoplay.stop();
                    },
                },
            });
        });
    }
}

