// MARKET IMAGE CAROUSEL | market.php
// ------------------------------------------------------------------
if (document.querySelector('.market-image-carousel')) {
    let marketImageCarousel = new Swiper(".market-image-carousel", {
        modules: [A, N, Px, EC],
        slidesPerView: 1,
        speed: 1000,
        spaceBetween: 0,
        loop: false,
        resistance: true,
        resistanceRatio: 0,
        parallax: true,
        effect: 'creative',
        allowTouchMove: false,
        creativeEffect: {
            prev: {
                scale: 1.5,
                opacity: 0,
                shadow: true,
            },
            next: {
                scale: 1.5,
                opacity: 0,
                shadow: true,
            },
        },
        autoplay: {
            delay: 5000,
            disableOnInteraction: false,
            pauseOnMouseEnter: true,
        },
        navigation: {
            nextEl: ".market-image-next",
            prevEl: ".market-image-prev"
        },
        breakpoints: {
            0: { allowTouchMove: false, },
            1025: { allowTouchMove: true, },
        },
    });

    // PAUSE ON MOUSE ENTER
    marketImageCarousel.autoplay.stop();
    let marketImageSection = document.querySelector('.market-section');
    let marketImageSlide = document.querySelectorAll('.market-image-carousel .swiper-slide');
    if (window.innerWidth > 1024) {
        marketImageSection.addEventListener('mouseenter', function () {
            marketImageCarousel.autoplay.stop();
        });
        marketImageSection.addEventListener('mouseleave', function () {
            marketImageCarousel.autoplay.start();
        });

        marketImageSlide.forEach(function (item, index) {
            gsap.to(item, {
                scrollTrigger: {
                    trigger: item,
                    start: "top-=300px center",
                    end: "bottom+=300px center",
                    scrub: true,
                    onEnter: function () {
                        marketImageCarousel.autoplay.start();
                    },
                    onLeave: function () {
                        marketImageCarousel.autoplay.stop();
                    },
                    onEnterBack: function () {
                        marketImageCarousel.autoplay.start();
                    },
                    onLeaveBack: function () {
                        marketImageCarousel.autoplay.stop();
                    },
                },
            });
        });
    }

    let marketIMGItem = document.querySelectorAll('.market-image-item');
    marketIMGItem[0].classList.add('is-active');

    marketIMGItem.forEach((item, index) => {
        item.addEventListener('click', () => {
            marketIMGItem.forEach((item, index) => {
                item.classList.remove('is-active');
            });
            item.classList.add('is-active');

            marketImageCarousel.slideTo(index);
        });
    });

    marketImageCarousel.on('slideChange', function () {
        marketIMGItem.forEach((item, index) => {
            item.classList.remove('is-active');
        });
        marketIMGItem[marketImageCarousel.realIndex].classList.add('is-active');
    });
}

